/* media query for breakpoints / responsiveness */
/* media query for motion preference */
/* colors */
:root {
  --clr-primary-soft-blue: 215 51% 70%;
  --clr-primary-cyan: 178 100% 50%;
  --clr-neutral-bg-main: 217 54% 11%;
  --clr-neutral-bg-card: 216 50% 16%;
  --clr-neutral-line: 215 32% 27%;
  --clr-neutral-white: 0 0% 100%;
}

/* border-radius */
/* box shadows */
/* ------------ */
/* Reset CSS    */
/* ------------ */
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list],
ul[class] {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* remove blue color and underline of a elements */
a {
  text-decoration: none;
  color: inherit;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    scroll-behavior: auto !important;
  }
}
body {
  font-family: Outfit, sans-serif;
}

.h1 {
  font-size: 1.375rem;
  font-weight: 600;
  line-height: 28px;
  color: hsl(var(--clr-neutral-white));
}
@media (min-width: 70rem) {
  .h1 {
    font-size: 1.375rem;
  }
}

.p {
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 26px;
  color: hsl(var(--clr-primary-soft-blue));
}
@media (min-width: 70rem) {
  .p {
    font-size: 1.125rem;
  }
}

.p-small {
  font-size: 0.9375rem;
  font-weight: 300;
  line-height: 19px;
  color: hsl(var(--clr-primary-soft-blue));
}
@media (min-width: 70rem) {
  .p-small {
    font-size: 1rem;
  }
}

.text-cyan {
  color: hsl(var(--clr-primary-cyan));
}

.text-white {
  color: hsl(var(--clr-neutral-white));
}

.text-bold {
  font-weight: 600;
}

body {
  background-color: hsl(var(--clr-neutral-bg-main));
  color: hsl(var(--clr-neutral-white));
  height: 100%;
}

/* util classes */
.center-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.flex {
  display: flex;
}

.flex-jc-spbtw {
  justify-content: space-between;
}

.link {
  display: inline-block;
  cursor: pointer;
}
.link:hover, .link:focus-visible {
  color: hsl(var(--clr-primary-cyan));
}

.card {
  display: inline-bl;
  background-color: hsl(var(--clr-neutral-bg-card));
  height: min-content;
  width: min-content;
  padding: 1.5rem;
  border-radius: 0.9375rem;
  box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.0952917);
}
.card__img-link {
  display: block;
  position: relative;
  background-image: url("/app/assets/images/image-equilibrium.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 278px;
  height: 278px;
  border-radius: 0.5rem;
}
.card__img-link::before {
  content: url("/app/assets/images/icon-view.svg");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 400ms ease-in-out;
  visibility: none;
  opacity: 0;
}
@media (prefers-reduced-motion: reduce) {
  .card__img-link::before {
    transition: none;
  }
}
.card__img-link:hover {
  background-color: hsl(var(--clr-primary-cyan)/0.5);
  background-blend-mode: overlay;
  transition: background-color 400ms ease-in-out;
  cursor: pointer;
}
@media (prefers-reduced-motion: reduce) {
  .card__img-link:hover {
    transition: none;
  }
}
.card__img-link:hover::before {
  visibility: visible;
  opacity: 1;
}
.card__content {
  margin-top: 1.5rem;
}
.card__title {
  margin-bottom: 0.75rem;
}
@media (min-width: 70rem) {
  .card__title {
    margin-bottom: 1rem;
  }
}
.card__description {
  padding-bottom: 1rem;
}
@media (min-width: 70rem) {
  .card__description {
    padding-bottom: 1.5rem;
  }
}
.card__price, .card__days {
  flex-basis: 50%;
  display: flex;
  gap: 0.75rem;
}
.card__days {
  justify-content: flex-end;
}
.card__footer {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid hsl(var(--clr-neutral-line));
}
@media (min-width: 70rem) {
  .card__footer {
    margin-top: 1.5rem;
  }
}
.card__footer img {
  width: 33px;
  border: 1px solid hsl(var(--clr-neutral-white));
  border-radius: 50%;
}